.header{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.heading_button{
    margin-top: 50px;
    display: flex;
    justify-content: center;

}
.header_p1{
    display: inline;
    position: relative;
    left: 12px;
    color: #FFFFFF;
    width: 108px;
    height: 32px;
    padding:8px 15px;
    color: #ffffff;
    background: #81C43B;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    border-radius: 100px;
    z-index: 2;
}

.header_p2{
    display: inline;
    width: 209px;
    height: 32px;
    padding:6px 20px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: #35414B;
    background: #F2F9EB;
    border-radius: 0px 100px 100px 0px;
    z-index: 1;
}

.header_icon{
    position: relative;
    top: 2px;
}

.heading{
    margin:  20px auto;
}

.main_heading{
    width: 825px;
    height: 144px;
    margin-left: 40px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: 64px;
    line-height: 72px;
    text-align: center;
    color: #35414B;
}

.header_para{
    margin: 20px auto;
    width: 505px;
    height: 48px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #4E5A65;
}
.input_form{
    width: 490px;
    margin: 0px auto;
}
.input_Email{
    width: 319px;
    height: 52px;
    font-family: 'Inter';
    font-style: normal;
    font-size: 16px;
    line-height: 16px;
    font-weight: 400;
    padding: 18px 99px 18px 24px;
    background: #FFFFFF;
    border: 1px solid #E1E5EA;
    border-radius: 4px 0px 0px 4px;
}

.input_button{
    width: 164px;
    height: 52px;
    font-family: 'Inter';
    font-style: normal;
    font-size: 16px;
    line-height: 16px;
    font-weight: 500;
    color: #FFFFFF;
    background: #794CFF;
    border: 1px solid #794CFF;
    border-radius: 0px 4px 4px 0px;
    text-align: center;  
}

.graph_img{
    margin-top: 50px;
    width: 896px;
    height: 646.29px;
    background-image: url(./Images/Graph\ 2.png);
    object-fit: cover;
    background-repeat: no-repeat;
    box-shadow: 0px 0px 48px -8px rgba(29, 40, 48, 0.18);
}

.statement{
    width: 397px;
    height: 16px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    margin: auto;
    margin-top: 60px;
    text-align: center;

    color: #4E5A65;
}
.bold1{
    font-weight: 1000;
}

.bottom_logo{
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    margin-top: 40px;
    text-align: center;
   

}
li img{
    padding: auto 30px;
}

.logo1 img{
    height: 25px;
    width: 80px;
    background: transparent;
}
.logo2 img{
    height: 25px;
    width: 80px;
    background: transparent;
}
.logo3 img{
    height: 20px;
    width: 80px;
    background: transparent;
}
.logo4 img{
    height: 25px;
    width: 80px;
    background: transparent;
}
.logo5 img{
    height: 25px;
    width: 80px;
    background: transparent;
    background-size: cover;
}