*{
    background-color:  #FBFAFE;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.navbar{
    
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 30px 100px;
    font-size: 16px;

}
.logo{
    font-family: 'Josefin Sans', sans-serif;
    font-style: normal;
    font-size: 24px;
    font-weight: 400;
    line-height: 24px;
    margin-right: 150px;
}
.nav_items{
    margin-left: 20px;
}
.nav_items li{
    display: inline-block;
}
.nav_icon1{
    position: relative;
    top: 4px;
    right: 1px;
}
.nav_icon2{
    position: relative;
    top: 4px;
    right: 1px;
}
.nav_icon3{
    position: relative;
    top: 4px;
    right: 1px;
}

li{
    font-family: 'Inter', sans-serif;
    font-style: normal;
    list-style-type: none;
    font-size: 16px;
    font-weight: 400;
    padding: 0px 20px;
  
}

.nav_buttons{
    font-family: 'Inter', sans-serif;
    background: transparent;
}
.nav_buttons .button{
    font-weight:400;
    margin: 0 20px ;
    border: none;
    font-size: 16px;
}

.button1{
    width: 169px;
    height: 48px;
    margin: 0 10px;
    padding: 10px 20px;
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    font-style: normal;
    color: #794CFF;
    background: #ECE6FF;
    border-radius: 4px;
    border: none;
    cursor: pointer;
}

